/*App.css - Styling the CV Application*/

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Nunito", serif;
}

.App {
  margin: 20px auto;
  max-width: 95%;
  font-family: sans-serif;
}

.Header {
  margin-bottom: 10px;
  font-weight: 700;
  text-align: center;
}

.General {
  margin: auto;
}

form {
  background-color: lightblue;
  border-radius: 5px;
  max-width: 90%;
  margin: 20px auto;
  padding: 20px 20px 10px 20px;
}

input,
textarea {
  display: block;
  width: 99%;
  margin: 10px auto;
  height: 40px;
  font-size: 1em;
}

button {
  border-radius: 5px;
  padding: 5px;
  font-size: 1em;
  text-align: center;
  margin: 10px;
}

.full-name {
  font-size: 2em;
  font-weight: 400;
}

.view {
  max-width: 80%;
  margin: auto;
  border: 1px solid;
  border-radius: 5px;
  padding: 20px;
}

.btn-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}

.view-btn-container {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  margin-top: 20px;
}

.general-view {
  margin-bottom: 10px;
  text-align: center;
  border-bottom: 1px solid;
}

.education-view,
.experience-view {
  display: flex;
  flex-flow: column nowrap;
  max-width: 100%;
  margin: 10px auto;
}

.other-personal {
  margin-bottom: 2px;
}

.degree,
.title {
  font-weight: 700;
  font-size: 1.2em;
}

.dates {
  font-weight: 300;
}